import { useState } from 'react';
import * as S from '../../../app.styles';
import AddDataPage from '../addData';

export default function PeriodPage(props: any) {
  const [aniMode, setAniMode] = useState(false);
  const [addData, setAddData] = useState(false);
  const [option, setOption] = useState(0);

  const btnArray = [
    '',
    '',
    '',
    '',
    '  (Buy-and-Hold)',
  ];

  const onClickOpenModal = () => {
    setAniMode((prev) => !prev);
  };

  const onClickAdd = () => {
    setAddData(true);
  };

  const optionEntity = (operation: string) => () => {
    if (operation === 'plus') {
      setOption((prev) => prev + 1);
    }
    if (operation === 'minus') {
      if (option === 0) return;
      setOption((prev) => prev - 1);
    }
  };

  return (
    <>
      <S.AllocationMenu aniMode={aniMode}>
        <li> </li>
        <li onClick={onClickOpenModal}>
          {btnArray[props.monthTab]}
          <S.AllocationList monthTab={props.monthTab} aniMode={aniMode}>
            {btnArray.map((el: any, i: number) => (
              <li id={String(i)} key={i} onClick={props.onClickMonthTab}>
                {el}
              </li>
            ))}
          </S.AllocationList>
        </li>
      </S.AllocationMenu>
      <S.Balance>
        <li> </li>
        <li>
          <input
            type='number'
            placeholder='   '
          />
        </li>
      </S.Balance>
      <S.Span>0~100%   . (0  )</S.Span>
      <S.Add>
        <h1> </h1>
        {addData && (
          <>
            {new Array(option).fill(1).map((_, i) => (
              <>
                <S.Data>
                  <AddDataPage i={i} />

                  <S.Balance>
                    <li></li>
                    <li>
                      <input
                        type='number'
                        defaultValue={100}
                        maxLength={100}
                        pattern='[0-9]*'
                      />
                    </li>
                  </S.Balance>
                  <S.Span>0~100   .</S.Span>
                </S.Data>
                <S.DelBtn onClick={optionEntity('minus')}></S.DelBtn>
              </>
            ))}
            <button onClick={optionEntity('plus')}></button>
          </>
        )}
        {!addData && <button onClick={onClickAdd}></button>}
      </S.Add>
    </>
  );
}
